.square {
  font-size: 50px;
  width: 50px;
  height: 50px;
  text-align: center;
  border: 1px solid #999;
  display: inline-block;
  /* Удалите отрицательные значения margin */
  margin-right: -1px;
  margin-top: -1px;
}

.winning {
  background-color: yellow;
}

.X {
  color: black;
}

.O {
  color: black;
}
